.header {
    display: flex;
    align-items: center;
    transition: transform 0.3s ease-in-out;

  }
  .header:hover {
    transform: scale(1.02); 
  }
.profile-picture {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  transition: transform 0.3s ease-in-out;
}
  
.profile-picture:hover {
  transform: scale(1.3);
}
  .header-text {
    text-align: left;
  }
  
  h1 {
    margin: 0;
    font-size: 2em;
  }
  
  h2 {
    margin: 0;
    font-size: 1.5em;
    color: gray;
  }
  
  p {
    font-size: 1em;
    margin-top: 10px;
  }
  